<template>
  <div id="review-card" class="py-2">
    <v-card flat>
      <v-card-text>
        <h2 class="text-h6 mb-2">Choose strengths</h2>
        <v-chip-group
          v-model="applicant_strength"
          column
          multiple
          active-class="red--text"
        >
          <v-chip filter v-for="(strength, index) in strengths" :key="index">
            {{ strength }}
          </v-chip>
        </v-chip-group>
      </v-card-text>
      <v-card-text>
        <v-textarea v-model="comment" color="red">
          <template v-slot:label>
            <div>Your thoughts. <small>(required)</small></div>
          </template>
        </v-textarea>
      </v-card-text>
      <v-card-text class="mb-3">
        <v-rating
          v-model="rating"
          color="red darken-3"
          background-color="grey darken-1"
          class="text-center"
          empty-icon="$ratingFull"
          half-increments
          hover
          x-large
        ></v-rating>
      </v-card-text>
      <v-btn @click="saveReview" block color="error">
        Save Review
      </v-btn>
    </v-card>
    <v-snackbar
      :value="alert"
      absolute
      :timeout="timeout"
      top
      :color="alert_color"
      right
      text
    >
      {{ alert_message }}
    </v-snackbar>
  </div>
</template>

<script>
import ApplicantDataService from "@/services/ApplicantDataService";

export default {
  name: "review-card",
  data: () => ({
    comment: null,
    rating: null,
    alert: false,
    alert_color: null,
    alert_message: null,
    timeout: 3000,
    applicant_strength: [],
    strengths: [
      "Adaption",
      "Administration",
      "Advising",
      "Bookkeeping",
      "Budgeting",
      "Checking",
      "Communication",
      "Compilation",
      "Coordination",
      "Counseling",
      "Creativity",
      "Teaching",
      "Tracking",
      "Training",
      "Troubleshooting",
    ],
  }),
  props: {
      item: {
          type: Object
      }
  },
  methods: {
    saveReview() {
      ApplicantDataService.leaveReview(this.$props.item.id, {
        rating: this.rating,
        proctor: this.$store.getters.current_user.proctor_id,
        comment: this.comment,
        strengths: this.applicant_strength.map(x => this.strengths[x]),
      })
        .then((res) => {
          this.expanded = []
          this.rating = null
          this.comment = ""
          this.applicant_strength = []
          this.alert_message = res.message;
          this.alert_color = 'success'
          this.alert = true;
          ApplicantDataService.update(this.$props.item.id)
            .then((res) => console.log(res))
            .catch((err) => console.error(err));
        })
        .catch((err) => {
          this.alert_message = err.message;
          this.alert_color = 'warning'
          this.alert = false;
        });
    },
  },
};
</script>

