<template>
  <v-card>
    <v-card-title>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="applicants"
      loading-text="Loading... Please wait"
      :search="search"
    >
      <template v-slot:item.status="{ item }">
        <v-chip :color="item.status ? 'error' : 'grey'" dark>
          {{ item.status ? 'Accepted' : 'Pending' }}
        </v-chip>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: "stage-one",
  created() {
    this.$store.dispatch('loadApplicants')
  },
  data: () => ({
    search: "",
    headers: [
      { text: "ID", value: "id" },
      { text: "Name", value: "name" },
      { text: "Email", value: "email" },
      { text: "Phone", value: "phone_number" },
      { text: "Date of birth", value: "dob" },
      { text: "Status", value: "status" },
    ],
  }),
  computed: {
    ...mapGetters(['applicants'])
  }
};
</script>

